<template>
  <div class="home">
    <Header />
    <b-container>
      <h2>News</h2>
      <b-row>
        <b-col md='6' lg='4' v-for="item in news" :key="item.id">
          <a :href="item.url" target="_blank">
            <div class="news-card card">
              <div class="wrapper" v-bind:style="{ backgroundImage: 'url(' + item.urlToImage + ')' } ">
                <div class="header">
                  <div class="date">
                    <span class="day">{{ item.publishedAt | moment("DD-MM-YYYY") }}</span>
                  </div>
                </div>
                <div class="data">
                  <div class="content">
                    <span class="author">{{ item.author }}</span>
                    <h4 class="title">{{ item.title }}</h4>
                    <a href="#" class="button">Read more</a>
                  </div>
                </div>
              </div>
            </div>
          </a>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import Header from '@/components/Header'
import moment from 'moment'

export default {
  name: 'Home',
  components: {
    Header
  },
  data () {
    return {
      news: {}
    }
  },
  mounted () {
    this.getNews()
  },
  methods: {
    getNews () {
      var currentdate = moment(new Date()).format('YYYY-MM-DD')
      this.$axios({
        method: 'GET',
        url: 'https://newsapi.org/v2/everything?q=bitcoin&from=' + currentdate + '&sortBy=publishedAt&apiKey=cff97a7fecba42e0abc462e4a7e998a3'
      })
        .then((response) => {
          this.news = response.data.articles
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
}
</script>

